<template>
  <div class="box" ref="box">
    <h2>预览</h2>
    <div style="position: relative;">
      <div class="home" @mouseenter="open">
        <iframe :src="getPhoneMsg" frameborder="0" width="360px" height="600px" id="frame" style="border: 1px solid gainsboro"></iframe>
      </div>
      <div class="close_phone" v-if="show" @mouseleave="close" @click="enter">
        <p>请进入预览模式查看</p>
        <img :src="src" alt="" style="width: 1.5rem;height: 1.5rem">
      </div>
    </div>
    <footer>
        <a href="javascript:void(0)" class="see" @click="enter" v-if="see">进入预览模式</a>
        <a href="javascript:void(0)" class="see" @click="out" v-if="!see">退出预览模式</a>
        <Tooltip placement="top" content="在手机上预览">
          <a href="javascript:void(0)" class="phone" @click="seeCode">
            <Icon type="iphone" size="22"></Icon>
          </a>
        </Tooltip>
      </footer>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        show: false,
        see: true,
        state: 1,
        appid: "",
        src: "",
        data: ''
      }
    },
    methods: {
      seeCode() {
        this.show = true;
        this.post("custom/loop/get_qrcode", {}).then(res => {
          if(res.data.code === 10000) {
            this.src = "http://weapp.xiguawenhua.com/" + res.data.data
          } else {}
        })
      },
      open() {
        this.state === 1 ? this.show = true : this.show = false;
      },
      close() {
        this.show = false;
      },
      enter() {
        this.state = 2;
        this.see = false;
        this.$refs.box.style.position = 'fixed';
        //        this.$refs.box.style.left=0;
        this.$refs.box.animate([{
          left: "100%",
          width: "30%"
        }, {
          left: 0,
          width: "100vw"
        }], {
          duration: 2000,
          fill: 'forwards',
          easing: 'ease'
        });
      },
      out() {
        this.state = 1;
        this.see = true;
        this.$refs.box.style.position = 'fixed';
        //      this.$refs.box.style.left="";
        this.$refs.box.animate([{
          left: 0,
          width: "100vw"
        }, {
          left: "70%",
          width: "6rem"
        }], {
          duration: 2000,
          fill: 'forwards',
          easing: 'ease'
        });
      }
    },
    computed: {
      getPhoneMsg() {
        //      if(document.getElementById('frame')!==null){
        //        document.getElementById('frame').contentWindow.location.reload(true);
        //      }
        return this.data;
      }
    },

  mounted(){
        this.appid=this.$route.params.appid;
        let url=JSON.stringify(this.$store.getters.getPhoneMsg);
        let data=encodeURIComponent(url);
        this.data=`//weapp.xiguawenhua.com/mobile/shop/#/home?data=${data}`;
    }
  }
</script>
<style scoped>
  @import url('../../../static/preview.css');
</style>



