<template>
  <div class="layout">
    <top></top>
    <div class="flex">
      <div class="layout-menu-left">
        <div class="layout-logo-left">
          <div class="app_name">
            <div style="margin-left: -0.4rem;flex: 0 0 0.05rem;">
              <img :src='pulishState["pic"]'>
            </div>
            <div style="margin-left: 0.2rem;margin-bottom: 0.2rem;">
              <span v-text='pulishState["name"]' style="display: inline-block;margin-bottom: 0.05rem;font-size: 0.14rem;"></span><br />
              <!--0授权 3发布 -->
              <span class="send" v-if="publish===0">未发布</span>
              <span class="send" v-if="publish===1">已绑定</span>
              <span class="send" v-if="publish===2">代码已上传</span>
              <span class="send" v-if="publish===3">审核中</span>
              <span class="send" v-if="publish===4">已上线</span>
            </div>
          </div>

          <div>
            <img src="../assets/boundary.png" />
          </div>
        </div>

        <ul class="layout-aside">
          <li @click="changeShop">
            <div style="margin-left: 0.2rem;">
              <i class="iconfont icon-shangdian i" style="margin-left: 0.19rem;"></i>
              <span>商店管理</span>
            </div>
          </li>
          <li v-show="shopShow" @click="info(1)" class="guan" @mouseover="enter(1)" @mouseout="out(1)">
            <div class="lay_span">
              <span>商品</span>
            </div>
            <div v-if="item[1].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li v-show="shopShow" @click="info(2)" class="guan" @mouseover="enter(2)" @mouseout="out(2)">
            <div class="lay_span">
              <span>微信支付</span>
            </div>
            <div v-if="item[2].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li v-show="shopShow" @click="info(3)" class="guan" @mouseover="enter(3)" @mouseout="out(3)">
            <div class="lay_span">
              <span>配送</span>
            </div>
            <div v-if="item[3].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li v-show="shopShow" @click="info(4)" class="guan" @mouseover="enter(4)" @mouseout="out(4)">
            <div class="lay_span">
              <span>商品分类</span>
            </div>
            <div v-if="item[4].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li v-show="shopShow" @click="info(5)" class="guan" @mouseover="enter(5)" @mouseout="out(5)">
            <div class="lay_span">
              <span>通知提醒</span>
            </div>
            <div v-if="item[5].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="info(6)" @mouseover="enter(6)" @mouseout="out(6)">
            <div>
              <i class="iconfont icon-jibenxinxi i"></i>
              <span>基本信息</span>
            </div>
            <div v-if="item[6].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="info(7)" @mouseover="enter(7)" @mouseout="out(7)">
            <div>
              <i class="iconfont icon-icon i"></i>
              <span>风格</span>
            </div>
            <div v-if="item[7].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="info(8)" @mouseover="enter(8)" @mouseout="out(8)">
            <div>
              <i class="iconfont icon-yonghuguanli i"></i>
              <span>用户管理</span>
            </div>
            <div v-if="item[8].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="info(9)" @mouseover="enter(9)" @mouseout="out(9)">
            <div>
              <i class="iconfont icon-xiaoxi i"></i>
              <span> 模板消息</span>
            </div>
            <div v-if="item[9].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="change">
            <div>
              <i class="iconfont icon-gongju i"></i>
              <span>营销工具</span>
            </div>
            <div v-if="item[10].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li v-show="rollShow" @click="info(10)" class="guan" @mouseover="enter(10)" @mouseout="out(10)">
            <div class="lay_span">
              <span>优惠券</span>
            </div>
            <div v-if="item[10].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
          <li @click="info(11)" @mouseover="enter(11)" @mouseout="out(11)">
            <div>
              <i class="iconfont icon-dingdan-copy i"></i>
              <span>订单管理</span>
            </div>
            <div v-if="item[11].show">
              <i class="iconfont icon-jiantou i"></i>
            </div>
          </li>
        </ul>
        <div class="btn">
          <a href="javascript:void(0)" @click="submitApp" v-if="publish===0">
            <i class="iconfont icon-xiaochengxu"></i>
            <span>授权小程序</span>
          </a>
          <a href="JavaScript:void(0)" style="cursor: not-allowed" v-else-if="publish===3">
            <i class="iconfont icon-xiaochengxu"></i>
            <span>审核中...</span>
          </a>
          <a href="JavaScript:void(0)" @click="submitApp" v-else-if="publish===1">
            <i class="iconfont icon-xiaochengxu"></i>
            <span>提交代码包</span>
          </a>
          <a href="JavaScript:void(0)" @click="submitApp" v-else>
            <i class="iconfont icon-xiaochengxu"></i>
            <span>立即发布</span>
          </a>
          <a href="JavaScript:void(0)" @click="closeEditor" class="list">
            <i class="iconfont icon-ccgl-dingdanguanli-3" style="margin-left: -0.1rem;"></i>
            <span>退出编辑器</span>
          </a>
        </div>

      </div>
      <div class="content">
        <info v-if="$route.name==='index'"></info>
        <!--<keep-alive>-->
        <router-view v-if="$route.name!=='index'"></router-view>
        <!--</keep-alive>-->
      </div>
      <div class="photo" v-if="pShow">
        <home></home>
      </div>
    </div>
    <Modal v-model="audit">
      <p slot="header" style="text-align:left">
        <span>小程序</span>
      </p>
      <div style="text-align:center;" class="program_content">
        <Icon type="android-time" size="50" color="#15bbfc"></Icon>
        <h2>已成功提交审核</h2>
        <p style="color:#90969e;font-size: 0.14rem;padding: 0.05rem">审核大约需要1-2天,结果将通过微信告知。</p>
        <p style="color:#90969e;font-size: 0.14rem">若审核通过,约2小时后将发布生效</p>
        <a href="javascript:void(0)" @click="audit=false"> 好的 </a>
      </div>
      <div slot="footer">
        <p style="display: flex;justify-content: center;align-items: center;padding: 0.1rem 0">

        </p>
      </div>
    </Modal>
    <Modal v-model="submit">
      <p slot="header" style="text-align:left">
        <span>小程序</span>
      </p>
      <div style="text-align:center;" class="program_content">
        <img :src="programs[0].src" alt="" style="width: 50px;max-height: 100%">
        <h2>{{programs[0].name}}</h2>
        <p style="color:#666666;font-size: 0.13rem">{{programs[0].title}}</p>
        <a href="javascript:void(0)" v-if="!msgBtn" @click="register">注册小程序账号</a>
        <a href="javascript:void(0)" @click="go" style="background-color:darkgrey;" v-if="!msgBtn">已有账号,前往小程序授权</a>
        <a href="javascript:void(0)" @click="success" v-if="msgBtn">成功授权</a>
        <a href="javascript:void(0)" @click="go" style="background-color: orangered;" v-if="msgBtn">授权失败,重新授权</a>
      </div>
      <div slot="footer">
        <p style="display: flex;justify-content: center;align-items: center;padding: 0.1rem 0">
          <Icon type="information-circled" size="18"></Icon> <span style="margin-left: 0.1rem;font-size: 0.13rem;color: #666666">提醒：请授权微信小程序账号，非公众号。</span>
        </p>
      </div>
    </Modal>
  </div>
</template>

<script>
  import top from './totalHead.vue'
  import leftBar from './public/left.vue'
  import info from './store/info/info.vue'
  import home from './phone/shoping.vue'
  export default {
    data() {
      return {
        theme: "light",
        msgBtn: false,
        programs: [{
            name: "微信小程序授权",
            title: '首次发布将需要进行小程序授权，请使用小程序管理员账号进行操作。',
            src: require('../assets/icon-wechat.svg')
          },
          {
            name: "微信小程序未进行微信认证",
            title: '未完成微信认证的小程序账号将无法使用微信支付功能',
            src: require('../assets/icon-wechat-verification.svg'),
          }
        ],
        txMsg: true,
        submit: false,
        //      审核
        audit: false,
        appid: '',
        modal: false,
        rollShow: false,
        shopShow: false,
        //      选中的
        num: 1,
        item: [{
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }],
        list() {
          const lists = document.getElementsByClassName('layout-aside')[0].getElementsByTagName('li');
          return lists
        },
        //状态
      }
    },
    computed: {
      publish() {
        return this.$store.getters.getPublish;
      },
      pShow() {
        return this.$store.getters.getStatus;
      },
      pulishState() {
        //          第一次登录
        if(this.getSession["state"] === undefined || this.getSession["state"] !== 1) {
          return this.getSession = this.$store.getters.getBaseInfo;
        } else {
          //              不同程序切换不同界面刷新
          if(this.$store.getters.getBaseInfo["pic"] === undefined) {
            return this.getSession
          } else {
            return this.getSession = this.$store.getters.getBaseInfo;
          }
        }
      },
    },
    methods: {
      //        解决首次加载出现问题
      submitApp() {
        this.post('custom/Own/getAppInfo', {
          author:1
        }).then(res => {
          if(res.data.code === 10000) {
            if(res.data.data.is_publish === 0) {
              return this.submit = true;
            } else if(res.data.data.is_publish === 2) {
              return this.post('/custom/weapp/submit_audit', {}).then(res => {
                if(res.data.code === 10000) {
                  this.audit = true;
                  this.$store.dispatch('publish', res.data.data.is_publish);
                } else {
                  const content = res.data.msg + "请重新发布";
                  this.$Modal.error({
                    title: "发布小程序",
                    content: content
                  });
                }
              })
            } else if(res.data.data.is_publish === 1) {
              return this.post('/custom/weapp/commit', {}).then(res => {
                if(res.data.code === 10000) {
                  this.$store.dispatch('publish', res.data.data.is_publish);
                } else {
                  const content = " 上传代码包失败，请重新上传";
                  this.$Modal.error({
                    title: "小程序",
                    content: content
                  });
                }
              })
            }
          } else {
            this.$Modal.error({
              title: "小程序授权失败",
              content: res.data.msg
            });
          }
        });
      },
      //    箭头显示
      imgShow(val) {
        this.item.map(list => {
          return list.show = false
        });
        this.item[val].show = true;
      },

      changeShop() {
        this.rollShow = false;
        this.shopShow = !this.shopShow
      },
      ok() {},
      cancel() {},
      //    orderList() {
      //      this.$router.push(`/${this.appid}/editor/orderList`);
      //    },
      change() {
        this.rollShow = !this.rollShow;
        this.shopShow = false
      },
      //      close展开项
      close() {
        this.rollShow = false;
        this.shopShow = false;
      },
      info(val) {
        this.num = val;
        this.item.map(list => {
          return list.show = false
        });
        this.item[val].show = true;
        let list = this.list();
        for(let item of list) {
          item.className = '';
        }
        switch(val) {
          case 1:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/goods`);
            this.$store.commit('setStatus', true);
            break;
          case 2:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/pay`);
            this.$store.commit('setStatus', true);
            break;
          case 3:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/delivery`);
            this.$store.commit('setStatus', true);
            break;
          case 4:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/sorts`);
            this.$store.commit('setStatus', true);
            break;
          case 5:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/inform`);
            this.$store.commit('setStatus', true);
            break;
          case 6:
            this.close();
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/info`);
            this.$store.commit('setStatus', true);
            break;
          case 7:
            this.close();
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/style`);
            this.$store.commit('setStatus', true);
            break;
          case 8:
            this.close();
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/user`);
            this.$store.commit('setStatus', true);
            break;
          case 9:
            this.close();
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/template`);
            this.$store.commit('setStatus', true);
            break;
          case 10:
            list[11].className = 'active';
            this.$router.push(`/${this.appid}/editor/card`);
            this.$store.commit('setStatus', true);
            break;
          case 11:
            list[12].className = 'active';
            this.$router.push(`/${this.appid}/editor/orderList`);
            this.$store.commit('setStatus', false);
            this.rollShow = false;
            break;
        }
      },
      enter(val) {
        this.item[val].show = true;
      },
      out(val) {
        this.item[val].show = false;
        this.item[this.num].show = true;
      },
      closeEditor() {
        this.$router.push('/establish');
//        清理appid
        sessionStorage.removeItem('appid');
      },
      go() {
        this.msgBtn = true;
        let url = `//weapp.xiguawenhua.com/weixin/component/auth_jump?apps=${this.appid}`;
        window.open(url, self);
      },
      register(){
        let url = `//mp.weixin.qq.com/wxopen/waregister?action=step1`;
        window.open(url, self);
      },
      success() {
        location.reload();
      }
    },
    components: {
      home,
      info,
      leftBar,
      top
    },
    created() {

    },
    mounted() {
      this.appid = this.$route.params.appid;
      let name = this.$route.name;
      let list = this.list();
      switch(name) {
        case 'goods':
          list[1].className = 'active';
          this.imgShow(1);
          this.num = 1;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'pay':
          list[2].className = 'active';
          this.imgShow(2);
          this.num = 2;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'delivery':
          list[3].className = 'active';
          this.imgShow(3);
          this.num = 3;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'sorts':
          list[4].className = 'active';
          this.imgShow(4);
          this.num = 4;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'inform':
          list[5].className = 'active';
          this.imgShow(5);
          this.num = 5;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case "index":
        case 'info':
          list[6].className = 'active';
          this.imgShow(6);
          this.num = 6;
          this.shopShow = false;
          this.rollShow = false;
          break;
        case 'style':
          list[7].className = 'active';
          this.imgShow(7);
          this.num = 7;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'user':
          list[8].className = 'active';
          this.imgShow(8);
          this.num = 8;
          this.shopShow = false;
          this.rollShow = false;
          break;
        case 'template':
          list[9].className = 'active';
          this.imgShow(9);
          this.num = 9;
          this.shopShow = false;
          this.rollShow = false;
          break;
        case 'card':
          list[11].className = 'active';
          this.imgShow(10);
          this.num = 10;
          this.shopShow = false;
          this.rollShow = true;
          break;
        case 'orderList':
          list[12].className = 'active';
          this.imgShow(11);
          this.num = 11;
          this.shopShow = false;
          this.rollShow = true;
          break;

      }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="scoped">
  @import url("../../static/common.css");
</style>



