<template>
    <div class="layout-menu-left">
      <div class="layout-logo-left">
        <div class="logo">
        </div>
        <div class="app_name">
          <img :src="pulishState.pic" alt="">
          <span>{{pulishState.name}}</span>
          <!--0授权 3发布 -->
          <span class="send" v-if="publish===0">未发布</span>
          <span class="send" v-if="publish===1">已绑定</span>
          <span class="send" v-if="publish===2">代码已上传</span>
          <span class="send" v-if="publish===3">审核中</span>
          <span class="send" v-if="publish===4">已上线</span>
        </div>
      </div>
      <ul class="layout-aside">
        <li @click="changeShop" class="active" @mouseover="enter(0)" @mouseout="out(0)">
          <div>
            <i class="iconfont icon-1"></i>
            <span>商店管理</span>
          </div>
          <div v-if="item[0].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li v-show="shopShow" @click="info(1)" @mouseover="enter(1)" @mouseout="out(1)">
          <div class="lay_span">
            <span>商品</span>
          </div>
          <div v-if="item[1].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li v-show="shopShow" @click="info(2)" @mouseover="enter(2)" @mouseout="out(2)">
          <div class="lay_span">
            <span>微信支付</span>
          </div>
          <div v-if="item[2].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li v-show="shopShow" @click="info(3)" @mouseover="enter(3)" @mouseout="out(3)">
          <div class="lay_span">
            <span>配送</span>
          </div>
          <div v-if="item[3].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li @click="info(4)" v-show="shopShow" @mouseover="enter(4)" @mouseout="out(4)">
          <div class="lay_span">
            <span>商品分类</span>
          </div>
          <div v-if="item[4].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li v-show="shopShow" @click="info(5)" @mouseover="enter(5)" @mouseout="out(5)">
          <div class="lay_span">
            <span>通知提醒</span>
          </div>
          <div v-if="item[5].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li @click="info(6)" @mouseover="enter(6)" @mouseout="out(6)" class="active">
          <div>
            <i class="iconfont icon-info" style="font-size: 0.2rem"></i>
            <span>基本信息</span>
          </div>
          <div v-if="item[6].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li @click="info(7)" @mouseover="enter(7)" @mouseout="out(7)">
          <div>
            <i class="iconfont icon-style-edit" style="font-size: 0.2rem"></i>
            <span>风格</span>
          </div>
          <div v-if="item[7].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li @click="info(8)" @mouseover="enter(8)" @mouseout="out(8)">
          <div>
            <Icon type="person-stalker" size="23"></Icon>
            <span>用户管理</span>
          </div>
          <div v-if="item[8].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li @click="info(9)" @mouseover="enter(9)" @mouseout="out(9)">
          <div>
            <Icon type="document-text"  size="23" ></Icon>
            <span id="mo">模板消息</span>
          </div>
          <div v-if="item[9].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li @click="change" @mouseover="enter(10)" @mouseout="out(10)">
          <div>
            <i class="iconfont icon-libao" style="font-size: 0.2rem"></i>
            <span>营销工具</span>
          </div>
          <div v-if="item[10].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
        <li v-show="rollShow" @click="info(11)" @mouseover="enter(11)" @mouseout="out(11)">
          <div class="lay_span">
            <span>优惠券</span>
          </div>
          <div v-if="item[11].show">
            <Icon type="chevron-right"></Icon>
          </div>
        </li>
      </ul>
      <div class="btn">
        <a href=" " @click="submitApp" v-if="publish===0">
          <Icon type="ios-nutrition"></Icon>
          <span>授权小程序</span>
        </a>
        <a href="JavaScript:void(0)" @click="submitApp" v-if="publish===2">
          <Icon type="ios-nutrition"></Icon>
          <span>立即发布</span>
        </a>
        <a href="JavaScript:void(0)" style="cursor: not-allowed" v-if="publish===3">
          <Icon type="ios-nutrition"></Icon>
          <span>审核中...</span>
        </a>
        <a href="JavaScript:void(0)" @click="orderList" class="list">
          <Icon type="ios-list"></Icon>
          <span>订单管理</span>
        </a>
        <footer>
          <a href="JavaScript:void(0)" @click="closeEditor">
            <Icon type="android-exit"></Icon>
            <span>退出编辑器</span>
          </a>
          <a href="JavaScript:void(0)">
            <Icon type="document-text"></Icon>
            <span>帮助文档</span>
          </a>
        </footer>
      </div>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        msgBtn: false,
        programs: [{
          name: "微信小程序授权",
          title: '首次发布将需要进行小程序授权，请使用小程序管理员账号进行操作。',
          src: require('../../assets/icon-wechat.svg')
        },
          {
            name: "微信小程序未进行微信认证",
            title: '未完成微信认证的小程序账号将无法使用微信支付功能',
            src: require('../../assets/icon-wechat-verification.svg'),
          }
        ],
        txMsg: true,
        submit: false,
        //      审核
        audit: false,
        appid: '',
        modal: false,
        rollShow: false,
        shopShow: false,
        //      选中的
        num: 1,
        item: [{
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: true
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        },{
          show: false
        }],
        list() {
          const lists = document.getElementsByClassName('layout-aside')[0].getElementsByTagName('li');
          return lists
        },
        //状态
      }
    },
    computed: {
      publish() {
        return this.$store.getters.getPublish;
      },
      pShow() {
        return this.$store.getters.getStatus;
      },
      pulishState() {
        return this.$store.getters.getBaseInfo;
      },
    },
    methods: {
      submitApp() {
        this.post('custom/Own/getAppInfo', {}).then(res => {
          if(res.data.code === 10000) {
            if(res.data.data.is_publish === 0) {
              this.submit = true;
            } else if(res.data.data.is_publish === 2) {
              this.post('/custom/weapp/submit_audit', {}).then(res => {
                if(res.data.code === 10000) {
                  this.audit = true;
                  this.$store.dispatch('publish', res.data.data.is_publish);
                } else {
                  const content = res.data.msg_test + "请重新发布";
                  this.$Modal.error({
                    title: "发布小程序",
                    content: content
                  });
                }
              })
            } else if(res.data.data.is_publish === 1) {
              this.post('/custom/weapp/commit', {}).then(res => {
                if(res.data.code === 10000) {
                  this.$store.dispatch('publish', res.data.data.is_publish);
                } else {
                  const content = " 上传代码包失败，请重新上传";
                  this.$Modal.error({
                    title: "小程序",
                    content: content
                  });
                }
              })
            }
          } else {}
        });
      },
      //    箭头显示
      imgShow(val) {
        this.item.map(list => {
          return list.show = false
        });
        this.item[val - 1].show = true;
      },

      changeShop() {
        this.rollShow = false
        this.shopShow = !this.shopShow
      },
      ok() {},
      cancel() {},
      orderList() {
        this.$router.push(`/${this.appid}/editor/orderList`);
      },
      change() {
        this.rollShow = !this.rollShow;
        this.shopShow = false
      },
      info(val) {
        this.num = val;
        this.item.map(list => {
          return list.show = false
        });
        this.item[val].show = true;
        let list = this.list();
        console.log(list);
        for(let item of list) {
          if(item.className === 'disabled') {
            item.className = 'disabled';
          } else {
            item.className = ''
          }
        }
        switch(val) {
          case 1:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/goods`);
            break;
          case 2:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/pay`);
            this.$store.commit('setStatus', true);
            break;
          case 3:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/delivery`);
            this.$store.commit('setStatus', true);
            break;
          case 4:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/sorts`);
            this.$store.commit('setStatus', true);
            break;
          case 5:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/inform`);
            this.$store.commit('setStatus', true);
            break;
          case 6:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/info`);
            this.$store.commit('setStatus', true);
            break;
          case 7:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/style`);
            this.$store.commit('setStatus', true);
            break;
          case 8:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/user`);
            this.$store.commit('setStatus', true);
            break;
          case 9:
            list[val].className = 'active';
            this.$router.push(`/${this.appid}/editor/template`);
            this.$store.commit('setStatus', true);
            break;
          case 11:
            list[val].className = 'active';
              this.$router.push(`/${this.appid}/editor/card`);
              this.$store.commit('setStatus', true);
              break;
           
        }
      },
      enter(val) {
        this.item[val].show = true;
      },
      out(val) {
        this.item[val].show = false;
        this.item[this.num].show = true;
      },
      closeEditor() {
        this.$router.push('/establish')
      },
      go() {
        this.msgBtn = true;
        let url = `http://weapp.xiguawenhua.com/weixin/component/auth_jump?apps=${this.appid}`;
        window.open(url, self);
      },
      success() {location.reload();}
    },
    mounted() {
      this.$store.dispatch("localBaseInfo");
      this.appid = this.$route.params.appid;
      let name = this.$route.name;
      let list = this.list();
      for(let item of list) {
        if(item.className === 'disabled') {
          item.className = 'disabled';
        } else {
          item.className = ''
        }
      }
      switch(name) {
        case 'goods':
          list[1].className = 'active';
          this.imgShow(2);
          this.num = 1;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case "index":
        case 'pay':
          list[2].className = 'active';
          this.imgShow(3);
          this.num = 2;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'delivery':
          list[3].className = 'active';
          this.imgShow(4);
          this.num = 3;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'sorts':
          list[4].className = 'active';
          this.imgShow(5);
          this.num = 4;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'inform':
          list[5].className = 'active';
          this.imgShow(6);
          this.num = 5;
          this.shopShow = true;
          this.rollShow = false;
          break;
        case 'info':
          list[6].className = 'active';
          this.imgShow(7);
          this.num = 6;
          this.shopShow = false;
          this.rollShow = false;
          break;
        case 'style':
          list[7].className = 'active';
          this.imgShow(8);
          this.num = 7;
          this.shopShow = false;
          this.rollShow = false;
          break;
        case 'user':
          list[8].className = 'active';
          this.imgShow(9);
          this.num = 8;
          this.shopShow = false;
          this.rollShow = false;
          break;
        case 'template':
          list[9].className = 'active';
          this.imgShow(10);
          this.num = 9;
          this.shopShow = false;
          this.rollShow = false;
          break;
        case 'card':
          list[11].className = 'active';
          this.imgShow(12);
          this.num = 11;
          this.shopShow = false;
          this.rollShow = true;
          break;
      }
    }
  }
</script>
<style scoped="scoped">
  .layout-menu-left {
    flex: 0 0 2.6rem;
    height: calc(100vh - 2px);
    background: #f5f5f5;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
  }

  .layout-menu-left::-webkit-scrollbar {
    width: 0;
  }

  .layout-aside {
    flex: 1;
  }

  .layout-aside>li {
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 0.1rem;
    justify-content: space-between
  }

  .layout-aside li:hover {
    background: #B8B4B4;
  }

  .active {
    background: #e7e7e7;
  }

  .layout-aside li i {
    margin: 0 0.1rem 0 0.2rem;
  }

  .layout-aside li span {
    font-size: 0.16rem;
  }
  .lay_span {
    margin-left: 50px;
  }
  .layout-logo-left {
    width: 90%;
    height: 1rem;
    border-radius: 0.03rem;
    margin: 0.15rem auto;
    display: flex;
    justify-content: center;
    align-items: center
  }

  .app_name>img {
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 50%;
  }

  .send {
    font-size: 12px;
    padding: 3px 5px;
    border-radius: 2px;
    border: solid 1px #747981;
    color: #747981;
    display: inline-block;
    height: 20px;
    line-height: 15px;
  }
  .btn>a {
    font-size: 0.14rem;
    color: white;
    display: inline-block;
    background: #795da3;
    border-radius: 0.05rem;
    width: 2.2rem;
    padding-top: 0.05rem;
    padding-bottom: 0.05rem;
    text-align: center;
    line-height: 0.4rem;
    margin: 0.05rem 0.22rem;
  }

  .btn>a>span {
    display: inline-block;
    padding: 0 0.1rem;
  }

  .btn>a:nth-child(2) {
    color: black;
    background: gainsboro;
  }

  .btn footer>a {
    margin-top: 0.1rem;
    display: inline-block;
    color: black;
    background: gainsboro;
    padding: 0.1rem 0.29rem;
    margin-left: -0.05rem
  }

  .slide-fade-enter-active {
    transition: all .3s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter,
  .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }

  h2 {
    margin: 0.2rem 0 0.2rem 0;
    font-weight: 600;
  }

  .modal_content div {
    margin-bottom: 0.3rem;
  }

  .modal_content img {
    width: 900px;
    max-height: 100%;
  }
  .program_content>p {
    font-size: 0.15rem;
  }

  .program_content>a {
    display: inline-block;
    width: 1.6rem;
    padding: 0.1rem;
    color: white;
    background: #93b719;
    border-radius: 0.05rem;
    margin-top: 0.4rem;
    margin-left: 0.1rem;
    margin-bottom: 0.1rem;
  }
  #mo{
    display: inline-block !important;
    margin-left: 10rem !important;
  }
</style>



